.heroImg{
    width: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    height: 60vh;
    position: relative;
}

.heading {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.heroImg::before{
    content: "";
    background: url("https://images.pexels.com/photos/4974912/pexels-photo-4974912.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.heroImg h1{
    font-size: 2.4rem;
}

.heroImg p{
    font-size: 1.4rem;
    text-align: center;
}

@media screen and (max-widthZ: 640px) {
    .heroImg h1{
        font-size: 2rem;
    }
}