.container {
    width: 100%;
    height: 100%;
}

.errorImg {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.8); 
    position: relative;
}

.errorImg::before{
    content: "";
    background: url(/src/assets/pngegg.png);
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}